import "./style.css";

import * as React from 'react';
import * as ReactDOM from 'react-dom';


const ResultDisplay = props => {
  const resultStyle = {
    height: "20px",
    color: "yellow",
    textAlign: "right",
    fontFamily: "sans-serif"
  };

  const expressionStyle = {
    height: "30px",
    color: "white",
    textAlign: "right",
    fontFamily: "sans-serif"
  };

  return (
    <div style={{ height: "50px", backgroundColor: "black" }}>
      <div style={resultStyle}>{props.result}</div>
      {
        props.message
          ? <div style={expressionStyle}>{props.message}</div>
          : <div style={expressionStyle}>
            {
              props.number
                ? props.expression + props.operator + props.number
                : props.expression
            }
          </div>
      }
    </div>
  );
};

const Button = props => {
  const className = "fluid ui button" + (props.color ? " " + props.color : "");
  const customStyle = {
    height: "100%",
    borderRadius: "0px",
    borderStyle: "solid",
    borderWidth: "1px",
  };

  return (
    <button
      style={customStyle}
      className={className}
      onClick={e => props.onClick(e)}
      value={props.content}
    >
      {props.content}
    </button>
  );
};

const defaultState = {
  expression: "",
  operator: "",
  number: "0",
  result: "",
  message: ""
};

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = defaultState;
    this.updateNumber = this.updateNumber.bind(this);
  }

  updateNumber(e) {
    const value = e.target.value;
    let result;
    let expression;
    switch (value) {
      case "AC":
        this.setState(() => defaultState);
        return;
      case "=":
        expression = this.state.expression + this.state.operator + this.state.number;
        result = eval(expression);
        this.setState(() => Object.assign({}, defaultState, { result }));
        return;
      case "*":
      case "/":
      case "-":
      case "+":
        expression = this.state.expression + this.state.operator + this.state.number;
        result = eval(expression);
        this.setState(() => ({ operator: value, result, expression, number: "" }));
        return;
      default:
        if (this.state.expression.length >= 25) {
          this.setState({ message: "Digit limit is met" });
        } else if (e.target.value == "." && this.state.expression.includes(".")) {

        } else if (!this.state.number || this.state.number === "0") {
          this.setState({ number: e.target.value });
        } else {
          this.setState({ number: this.state.number + e.target.value });
        }
        return;
    }
  }

  render() {
    return (
      <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100%" }}>
        <div className="grid" id="calculator-container">
          <div id="result">
            <ResultDisplay
              expression={this.state.expression}
              operator={this.state.operator}
              number={this.state.number}
              result={this.state.result}
              message={this.state.message}
            />
          </div>
          <div className="btn-size double-width" id="btn-ac">
            <Button content="AC" color="teal" onClick={this.updateNumber} />
          </div>
          <div className="btn-size" id="btn-divide">
            <Button content="/" color="teal" onClick={this.updateNumber} />
          </div>
          <div className="btn-size" id="btn-multiply">
            <Button content="*" color="teal" onClick={this.updateNumber} />
          </div>
          <div className="btn-size" id="btn-1">
            <Button content="7" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size" id="btn-2">
            <Button content="8" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size" id="btn-3">
            <Button content="9" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size" id="btn-minus">
            <Button content="-" color="teal" onClick={this.updateNumber} />
          </div>
          <div className="btn-size">
            <Button content="4" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size">
            <Button content="5" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size">
            <Button content="6" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size" id="btn-plus">
            <Button content="+" color="teal" onClick={this.updateNumber} />
          </div>
          <div className="btn-size">
            <Button content="1" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size">
            <Button content="2" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size">
            <Button content="3" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size double-height" id="btn-equal">
            <Button content="=" color="teal" onClick={this.updateNumber} />
          </div>
          <div className="btn-size double-width" id="btn-0">
            <Button content="0" color="grey" onClick={this.updateNumber} />
          </div>
          <div className="btn-size" id="btn-dot">
            <Button content="." color="grey" onClick={this.updateNumber} />
          </div>
        </div >
      </div>
    );
  }
}

